<template>
    <!-- 热歌榜 -->
     <div class="bigbox">
         <div class="bigimg">
             <div class="hottop">
                <div class="bigpic">
                    <div class="pic"></div>
                </div>
                <div v-if="playList" class="banner">
                    更新日期:{{playList.updateTime | formatDate}}
                </div>
            </div>
         </div>

         <ul class="newsong" v-if="hotSong.length>0">
             <li v-for="(s,index) in hotSong" :key="index">
                 <SongItem :item="s"><div class="musicIndex">{{index+1>9 ? (index+1) : '0' + (index+1)}}</div></SongItem>
             </li>
         </ul>
         
         <Loading v-if="isLoading"></Loading>
         <div class="loadingmore">{{ momore ? "没有更多了" : "正在加载..." }}</div>
     </div>
</template>
<script>
import SongItem from '@/components/SongItem'
import Loading from '@/components/Loading'
export default {
    name:"recommend",
    data(){
        return{
            hotSong:[],
            tSQ:[],
            playList:null,
            page:1,
            isLoading:true,
            momore:false
        }
    },
    created(){
        this.loadMusic();
        window.addEventListener("scroll",this.handleScroll);
    },
    methods:{
        loadMusic(){
            //获取歌单详情
            this.$axios.get('/playlist/detail?id=3778678').then(data=>{
                // console.log(data)
                //把traksID变成字符串
                let trackIDS= data.data.playlist.trackIds.slice((this.page-1)*20,20*this.page);
                if(!trackIDS) return{};
                this.playList=data.data.playlist;
                let ids = trackIDS.map(d=>{
                    return d.id;
                })
                return ids.join(",");
            }).then(ids=>{
                //获取歌曲详情
                this.$axios.get('/song/detail?ids='+ids).then(songData=>{
                    this.isLoading=false;
                    if(!songData.data.privileges) return{};
                    songData.data.privileges.map(d=>{
                        this.tSQ.push(d.playMaxbr)
                        return d;
                    })
                    songData.data.songs.map(d=>{
                        //处理数据和SongItem不一致 转换数据
                        d.song={
                            artists:d.ar
                        }
                        this.hotSong.push(d);//加入数组
                        return d;
                    })
                    //this.hotSong=songDat;//直接赋值
                });
            }).catch(err=>{
                console.log(err);
            })
        },
        handleScroll () { //滚动事件监听处理
            //是否滚动到底部的判断
            let scrolltop = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset;
            if (scrolltop + document.documentElement.clientHeight == document.documentElement.scrollHeight) {
                this.page ++;
                if (this.page>10) {
                    //如果大于总页数停止请求数据
                    this.momore = true;
                    return false;
                } else {
                    this.loadMusic();
                    this.isLoading = true;
                }
            }

        },
            // loadingMore(){
            //     this.page++;
            //     if(this.page>=10){
            //          return false
            //     }
            //     this.isLoading=true;
            //     this.loadMusic();
            // }
    },
    destoryed(){
        window.removeEventListener("scroll",this.handleScroll,false)
    },
    components:{
        SongItem,
        Loading
    },
    filters:{   
        formatDate(val){
            let date = new Date(val);
            return(date.getMonth()+1)+'月'+(date.getDate())+'日';
        }
    }
}
</script>

<style lang="less" scoped>
.bigbox{
    margin-top: 6px;
    .bigimg{
        position: relative;
        padding-top: 38.9%;
        overflow: hidden;
        background: url(../assets/imgs/hot.jpg) no-repeat;
        background-size: contain;
        .hottop{
            justify-content: center;
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 2;
            padding-left: 20px;
            padding-top: 20px;
            box-sizing: border-box;
            .bigpic{
                width: 142px;
                height: 67px;
                background: url(../assets/imgs/index_icon_2x.png) -24px -30px no-repeat;
                background-size: 166px 97px;
            }
            .banner{
                margin-top: 10px;
                color: hsla(0,0%,100%,.8);
                font-size: 12px;
            }
        }
    }
    .newsong{
        padding: 0px 10px 0px 2px;
        li{
            &:nth-child(1){
                div>.musicIndex{
                    color: #df3436;
                }
            }
            &:nth-child(2){
                div>.musicIndex{
                    color: #df3436;
                }
            }
            &:nth-child(3){
                div>.musicIndex{
                    color: #df3436;
                }
            }
            div>.musicIndex{
                text-align: center;
                display: flex;
                flex: 1;
                align-items: center;
                justify-content: center;
                width: 40px;
                height: 55px;
                font-size: 17px;
                color: #999;
            }
        }
    }
    .loadingmore{
        text-align: center;
    }
}
</style>